.radio {
    position: relative;
    left: 0;
    top: 0;
    content: "";
    border: 2px solid #000;
    width: 23px;
    height: 23px;
    border-radius: 50%;
    cursor: pointer;
    appearance: none;
}

.radio::before {
}


.radio:checked::before {
    content: '';
    position: absolute;
    left: 3px;
    top: 3px;
    background-color: #000;
    width: 13px;
    height: 13px;
    border-radius: 50%;
}

.label {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 11px;
}

.span {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}

.options {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 22px;
}

